import Vue from 'vue'
//import App from './App.vue'
//import Hello from './components/Hello.vue'
//import Xx from './components/Xx.vue'
import filters from './filters'
require("./css/xx.less")

//  注册filters
Object.keys(filters).forEach(k => Vue.filter(k, filters[k]))


new Vue({
  el: 'body',
  components: {
    App:Vue.component('app', function (resolve) {
          require(['./App'], resolve)
      }),
    Hello:Vue.component('hello', function (resolve) {
      require(['./components/Hello.vue'], resolve)
    }),
    Xx:Vue.component('xx', function (resolve) {
      require(['./components/xx.vue'], resolve)
    })
  }
})



